export default {
	computed: {
		isOpen() {
			return this.$refs.multiselect.$refs.VueMultiselect.isOpen
		},
	},
	methods: {
		toggle() {
			this.$refs.multiselect.$refs.VueMultiselect.toggle()
			this.resetScroll()
		},

		resetScroll() {
			this.$refs.multiselect.$el.querySelector('.multiselect__content-wrapper').scrollTop = 0
		},

		getSearch() {
			return this.$refs.multiselect.$refs.VueMultiselect.search
		},

		resetSearch() {
			this.$refs.multiselect.$refs.VueMultiselect.search = ''
		},

		select(option) {
			this.$refs.multiselect.$refs.VueMultiselect.select(option)
		},
	},
}
